// Welcome to the main stylesheet

@import "vars";
@import "typography";

@import "components/nav";
@import "components/input";
@import "components/button";
@import "components/card";
@import "components/search-box";

@import "pages/search";

* {
	box-sizing: border-box; }

html {
	background-color: #FAFAFA;
	font-family: $font-normal; }
a.icon-link {
	text-decoration: none;
	color: inherit; }

.content {
	padding-top: 10em;
	&.no-top-padding {
		padding-top: 4em; } }

.container {
	margin-left: auto;
	margin-right: auto;
	width: 100%;
	max-width: 820px;
	&.steep {
		max-width: 528px; } }

.marg-b-1 {
	margin-bottom: 1em; }

@media screen and (max-width: 560px) {
	.container {
		padding-left: 20px;
		padding-right: 20px; } }
